@import "mixins";
@import "colors";

.focal {
	align-items: flex-start;
	display: flex;
	flex-wrap: wrap;
	gap: 2rem;

	h1 {
		color: $dsblue;
	}

	>* {
		padding: 2rem;
		flex: 1 1 100%;

		background-color: $darkergrey;

		@include breakpoint-up(medium) {
			flex: 1 1 calc(50% - 2rem);
		}

		@include breakpoint-up(large) {
			flex: 0 0 calc(33.33% - 2rem);
		}
	}
}


.box-link {
	background-color: $darkergrey;
	padding: 2rem;
	border-left: 0rem solid $dsblue;
	cursor: pointer;
	transition-duration: 0.15s;
	display: flex;
	flex-direction: row;
	gap: 1rem;
	align-items: center;
	justify-content: flex-start;
	justify-items: flex-start;

	&:hover {
		background-color: $offblack;
		color: $dsblue;

		svg {
			fill: $dsblue;
		}

		h2 {
			color: $lightgrey;
		}
	}

	svg {
		flex: 0 0 48px;
		fill: $midgrey;

		transition-duration: 0.15s;
	}

	>* {
		margin-bottom: 0;
	}
}

.demo-title {

	margin-bottom: 1rem;

	h1 {
		font-size: 1rem;
		letter-spacing: 0.2rem;
	}

	h2 {
		text-transform: none;
		;
		font-size: 0.8rem;
		letter-spacing: 0.1rem;
	}
}

.range-slider {
	display: flex;
	gap: 0.5rem;
	flex-wrap: nowrap;
	align-items: center;
	margin-bottom: 1rem;

	&__label {
		flex: 1 1 7rem;
		align-self: center;

		h2 {
			font-size: 0.8rem;
			letter-spacing: 0.1rem;
		}

		>* {
			margin-bottom: 0;
		}
	}

	&__input {
		flex: 1 1 100px;
		min-width: 10px;

		background-color: $darkergrey;
	}

	&__value-text {
		flex: 1 1 4rem;
		display: inline-block;
		position: relative;
		color: $lightgrey;
		text-align: center;
		border-radius: 3px;
		background: $darkergrey;


	}
}

.shader-area {
	width: 100%;
	position: relative;
	aspect-ratio: 1;
	margin-bottom: 1rem;
}